<template>
  <div id="app">
    <el-form :model="ruleForm" ref="ruleForm" label-width="0px" class="demo-ruleForm">
      <h5 class="color-white">Leave A Message</h5>
      <el-form-item label="" prop="FullName" required>
        <el-input v-model="ruleForm.FullName" placeholder="Full Name*"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Email" :rules="[
      { required: true, message: 'Email is required', trigger: 'blur' },
      { type: 'email', message: 'Email is required', trigger: ['blur', 'change'] }
    ]" :required=true>
        <el-input v-model="ruleForm.Email" placeholder="Email*"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Subject">
        <el-input v-model="ruleForm.Subject" placeholder="Subject"></el-input>
      </el-form-item>
      <el-form-item label="" prop="Message">
        <el-input type="textarea" style="font-family: 'Open Sans', sans-serif !important;" :autosize="{ minRows: 4, maxRows: 8}" v-model="ruleForm.Message" placeholder="Message"></el-input>
      </el-form-item>
      <el-form-item class="sub-btn">
        <el-button type="primary" :loading=loading :disabled=disableBtn @click="submitForm('ruleForm')">Send</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        disableBtn: false,
        loading: false,
        ruleForm: {
          FullName: '',
          Email: '',
          Subject:'',
          Message:'',
        },
      };
    },
    methods: {
      submitForm(formName) {
        const vm = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
            this.loading = true;
            vm.axios.post('https://api.zigi-tech.com/API/Message/sendMessage', vm.ruleForm).then((res) => {
                console.log(res);
                this.$message({
                  type:'success',
                  message:'success'
                });
                this.disableBtn = true;
                this.loading = false;
            });
          } else {
            this.$message({
              type:'error',
              message:'Something is required'
            });
            this.disableBtn = false;
            return false;
          }
        }).catch(() => {
          this.$message({
              type:'error',
              message:'Send fail'
            });
        });
      }
    }
  }
</script>

